.contact-us {
  &__info {
    margin: 0 0 24px;

    &-title {
      margin: 0 0 8px;
      font-weight: 400;
      font-size: 14px;
      line-height: 140%;
      color: #9fadc2;
    }

    &-content {
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: #3e3e3e;

      text-decoration: none;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
